<template>
    <div class="box-contenido">
        <MenuVue></MenuVue>
        <div class="box-info">
            <b-row>
                <b-col cols="12">
                    <h1>Consumo de API Placeholder con Axios</h1>
                </b-col>
            </b-row>

            <b-row>
                <b-col cols="12"> 
                    <h1>Cards</h1>
                    <b-row class="mt-5" cols="12" >
                        <b-col lg="4" md="6" class="mb-3" v-for="dato in datos" :key="dato.id">
                            <b-card class="mb-2" img-top>
                                <img :src="dato.thumbnailUrl" alt="" class="img-card">
                                <h4>{{dato.id}}</h4>
                                <p class="card-text">{{dato.title}}</p>
                                <b-button href="" variant="success">Ir</b-button>
                            </b-card>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
    import MenuVue from "@/components/MenuVue.vue"
    import  axios from 'axios'

    export default {
        name:'apiaxios',
        data(){
            return{
                datos:null,
            }
        },
        components:{
            MenuVue,
        },
        mounted() {
            this.getTodos();
        },
        methods: {
            getTodos(){
                axios
                .get('https://jsonplaceholder.typicode.com/photos?_start=0&_limit=5')
                .then(respuesta =>{
                   // console.log(respuesta)
                    this.datos = respuesta.data
                })
                .catch(erro => console.log(error))
            },
        },
    }

</script>