<template>
    <div class="box-contenido">
        <MenuVue></MenuVue>
        <div class="box-info">
            <b-row>
                <b-col cols="12" class="mb-2">
                    <h1>Implementacion de Modulos</h1>
                    <p>Este se puede implementar cuando se hace uso de muchos states, mutaciones, metodos entre otros esto con la finalidad de no sobrecargar el store por lo que se fracciona los modulos segun las tareas que se necesiten realizar</p>
                </b-col>
                <b-col>
                    <h4>Uso de los Modulos</h4>
                    <p>Se realizaron dos modulos: <strong>Tareas y Contador</strong></p>
                    <div class="div-mod">
                        <h3>Tareas</h3>
                        <ul v-for="(item, index) in arrayTareas" :key="index" >
                            <li>{{item}}</li>
                        </ul>
                    </div>
                    <div class="div-mod">
                        <h3>Contador</h3>
                        <h2>{{contadorWork}}</h2>
                        <div class="box-btn">
                            <b-button variant="success" @click="aumentarContador(1)" class="btn">+</b-button>
                            <b-button variant="danger" @click="disminuirContador(1)" class="btn">-</b-button>
                        </div>
                    </div>

                    <div class="">
                        <p>Tambien se pueden hacer uso de states golades desde la store</p>
                        <h4>{{titulo}}</h4>
                    </div>
                </b-col>
            </b-row>
            <hr>
            <b-row>
                <b-col>
                    <h2>Calculadora</h2>
                    <b-form>
                        <label>Núm1: <input type="number" v-model.number="n1" class="input-calcu"> </label>
                        <br>
                        <label>Núm2: <input type="number" v-model.number="n2" class="input-calcu"></label>
                        <div class="mt-3">
                            <p class="mb-0"><span class="span-calcu">Suma </span>: {{suma}}</p>
                            <p class="mb-0"><span class="span-calcu">Resta </span>: {{resta}}</p>
                            <p class="mb-0"><span class="span-calcu">Multiplicar </span>: {{mult}}</p>
                            <p class="mb-0"><span class="span-calcu">Dividir</span> : {{dividir}}</p>
                        </div>
                    </b-form>
                </b-col>
            </b-row>
            <hr>
            <b-row>
                <b-col>
                    <h2>Barra de Progreso</h2>
                        <div class="m-2">
                            <div class="btns-prog">
                                <button class="btn btn-primary" @click="contador++"> + </button>
                                <button class="btn btn-danger" @click="contador--"> - </button>
                            </div>
                            <h3 class="my-4 prog-cont">{{contador}}</h3>
                            <div class="progress">
                                <div class="progress-bar" role="progressbar" :style="{'width': contador + '%'}" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100" :class="color">{{contador}}%</div>
                            </div>
                        </div>
                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex"
import MenuVue from "@/components/MenuVue.vue"

export default {
    name:'variosVue',
       data () {
        return {
            title:"Calculadora Vue",
            n1:0,
            n2:0,
            contador:0
        }
    },
    computed: {
        suma() {
            return this.n1+this.n2;
        },
        resta() {
            return this.n1-this.n2;
        },
        mult() {
            return this.n1*this.n2;
        },
        dividir() {
            const result =  this.n1/this.n2; 
            return result === Infinity || result === -Infinity ? "Error no calculable" : result;
        },
        color() {
            return {
                'bg-success':this.contador <= 10,
                'bg-warnig': this.contador >10 && this.contador <20,
                'bg-danger': this.contador >= 20
            }
        },
        ...mapState('tareas',['arrayTareas']),
        ...mapState('contador', ['contadorWork']),
        ...mapState(['titulo']),
    },
    components:{
        MenuVue
    },
    methods: {
        ...mapMutations('contador', ['aumentarContador','disminuirContador']),
    },
}
</script>